.third{
    position: relative;
    background: url('/static/pintu/pt_bg_2.jpg') no-repeat top center / auto 100%;
    width: 100vw;
    height: 100vh;
    padding: 0 .28rem;
}
.third .tips{
    padding-top: 0.667rem;
    font-family: 'HappyZcool-2016f6f6a4aa81a623';
    font-size: .64rem;
    text-align: center;
    color: #b95757;
    text-shadow: 0 .04rem 0 rgba(56,38,37,.33),0 .04rem 0 rgba(56,38,37,.33);
}
.third .image{
    margin-top: .28rem;
}
.third .image>.title{
    font-size: .493rem;
    color: #383838;
    text-align: center;
}
.third .image>.img{
    width: 100%;
    margin-top: .653rem;
    width: 9.453rem;
    height: 9.453rem;
}
.third .btn-group{
    margin-top: 1rem;
}
.third .btn-group>button{
    width: 4.4rem;
    height: 1.387rem;
}
.third .btn-group>button.back{
    background: url('/static/pintu/pt_btn_back.png') no-repeat center / 100%;
    margin-right: .387rem;
}
.third .btn-group>button.return{
    background: url('/static/pintu/pt_btn_again.png') no-repeat center / 100%;
}


