.second{
    position: relative;
    background: url('/static/pintu/pt_bg_2.jpg') no-repeat top center / auto 100%;
    width: 100vw;
    height: 100vh;
    padding: 0 0.4rem;
}
.second .title{
    width: 100%;
    height: 1.813rem;
    line-height: 1.813rem;
    font-size: 0.493rem;
    font-family: 'PingFangSCMf6fa4c0a91a623';
    color: #383838;
}
.second .canvas{
    position: relative;
    width: 9.2rem;
    height: 9.2rem;
    border: .133rem solid #434343;
}
.second .desc{
    position: relative;
    padding-left: .5rem;
    margin-top: 0.48rem;
    font-size: 0.373rem;
    color: #999;
}
.second .desc:before{
    content: '';
    display: inline-block;
    background: url('/static/pintu/tips.png') no-repeat center / 100% auto;
    width: .4rem;
    height: 100%;
    position: absolute;
    left: 0;
}
.second .slice{
    position: relative;
    margin-top: 1.187rem;
    overflow-x: auto;
    overflow-y: hidden;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}



