body,div,button,p {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
button{
    outline: none;
    border: none;
    background: none;
}
*{
    -webkit-touch-callout:none;
    -moz-touch-callout:none;
    -ms-touch-callout:none;
    touch-callout:none;
}
@media screen and (min-width: 320px){html{font-size: 32px;} }
@media screen and (min-width: 360px){html{font-size: 36px;} }
@media screen and (min-width: 375px){html{font-size: 37.5px;} }
@media screen and (min-width: 384px){html{font-size: 38.5px;} }
@media screen and (min-width: 414px){html{font-size: 41.4px;} }
@media screen and (min-width: 448px){html{font-size: 44.8px;} }
@media screen and (min-width: 480px){html{font-size: 48px;} }
@media screen and (min-width: 512px){html{font-size: 51.2px;} }
@media screen and (min-width: 544px){html{font-size: 54.4px;} }
@media screen and (min-width: 576px){html{font-size: 57.6px;} }
@media screen and (min-width: 608px){html{font-size: 60.8px;} }
@media screen and (min-width: 640px){html{font-size: 64px;} }
@media screen and (min-width: 750px){html{font-size: 75px;} }
@media screen and (min-width: 800px){html{font-size: 80px;} }
@media screen and (min-width: 1024px){html{font-size: 102.4px;} }


