.first{
    position: relative;
    background: url('/static/pintu/pt_bg_1.jpg') no-repeat top center / auto 100%;
    width: 100vw;
    height: 100vh;
}
.first .logo{
    width: 100%;
    height: 5.907rem;
    background: url('/static/pintu/pt_logo.png') no-repeat center / 100%;
}
.first .image-list{
    position: relative;
    width: 100%;
    height: 4.453rem;
}
.first .image-list .item{
    position: absolute;
    width: 4.453rem;
    height: 4.453rem;
    padding: 0.15rem;
    background: white;
    border-radius: 0.16rem;
}
.first .image-list .item>img{
    width: 100%;
    height: 100%;
    max-width: 100%;
    max-height: 100%;
}
.first .image-list .btn-group{
    position: absolute;
    width: 100%;
    height: 100%;
    line-height: 4.453rem;
    text-align: ;
}
.first .image-list .btn-group>.btn-l{
    width: 1.04rem;
    height: 100%;
    background: url('/static/pintu/pt_left_1.png') no-repeat center / 100%;
    vertical-align: middle;
}
.first .image-list .btn-group>.btn-r{
    position: relative;
    left: calc(100% - 1.04rem * 2);
    width: 1.04rem;
    height: 100%;
    background: url('/static/pintu/pt_left_2.png') no-repeat center / 100%;
    vertical-align: middle;
}
.first .level{
    width: 100%;
    height: 3.267rem;
    line-height: 3.267rem;
    text-align: center;
    background: url('/static/pintu/pt_bar.png') no-repeat center / 100% auto;
}
.first .level>button{
    height: 0.773rem;
    /*display: inline-block;*/
    vertical-align: middle;
}
.first .level>button.btn-l{
    width: 0.707rem;
    background: url('/static/pintu/pt_left.png') no-repeat center / 100%;
}
.first .level>button.btn-r{
    width: 0.707rem;
    background: url('/static/pintu/pt_right.png') no-repeat center / 100%;
}
.first .level>button.content{
    width: 5.387rem;
    font-family: 'HappyZcool-2016f6f6a4aa81a623';
    font-size: 0.471rem;
    font-weight: normal;
    font-stretch: normal;
    color: #fff;
    text-shadow: 0 0 .067rem #8b9c50,0 0 .067rem #8b9c50,0 0 .067rem #8b9c50,0 0 .067rem #8b9c50,0 0 .067rem #8b9c50,0 0 .067rem #8b9c50,0 0 .067rem #8b9c50;
}
.first .start{
    text-align: center;
}
.first .start>button{
    width: 4.92rem;
    height: 1.827rem;
    background: url('/static/pintu/pt_start.png') no-repeat center / 100%;
}


